.contenedor-principal {
  background-color: $gris;

  .encabezado {
    display: none;
    @include tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: $grisMedio;
    }
    .semestre {
      margin-left: 1rem;
      display: flex;
      align-items: baseline;
      gap: 1rem;

      .openbtn {
        @include boton($blanco, inline-block);
        font-size: 3rem;
        color: $grisOscuro;
      }

      h2 {
        font-size: 2.5rem;
        color: $grisOscuro;
        margin: 0;
        font-weight: $bold;
      }
    }
  }
}

.perfil {
  &:hover .contenido-perfil {
    display: flex;
  }

  .boton-perfil {
    display: flex;
    font-size: 2rem;
    border: none;
    background-color: transparent;
    cursor: pointer;
    align-items: center;
    color: $grisOscuro;
  }
  i {
    font-size: 4rem;
  }

  .contenido-perfil {
    display: none;
    padding: 0.5rem 1rem;
    flex-direction: column;
    align-items: center;
    position: absolute;
    background-color: $blanco;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    width: 12.1rem;

    transition: visibility 0.1s ease-in-out, opacity 0.2s linear,
      height 0.2s linear;

    button {
      @include boton($azul, block);
      font-size: 1.5rem;
      width: 100%;
      padding: 0.5rem;
      color: $blanco;
    }
    a {
      width: 100%;
      .boton-salir {
        @include boton(red, inline-block);
      }
    }
  }

  .mostrar {
    visibility: visible;
    opacity: 1;
  }
}
