.contenedor-grupos {
  width: 98%;
  margin: 0 auto;
  height: 50rem;
  .titulo-grupos {
    width: 100%;
    h2 {
      text-align: center;
      font-weight: $bold;
      font-size: 2.5rem;
      margin-top: 1rem;
      margin-bottom: 1rem;
      color: $azul;
      text-transform: uppercase;
      @include tablet {
        text-align: left;
      }
      //  border-bottom: 0.2rem solid rgb(58, 58, 58);
      //  width: 30%;
    }
    p {
      text-align: center;
    }
  }

  .acciones-grupo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 90%;
    margin: 0 auto;
    background-color: #fefefe;
    padding: 1rem;
    border-radius: 1rem;
    a {
      font-size: 1.5rem;
      padding: 0.9rem;
    }
    @include tablet {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      margin: auto;
      width: 100%;
    }

    .buscar {
      width: 100%;
      input {
        height: 3.9rem;
        width: 23rem;
        border-top: 0px;
        border-left: 0px;
        border-right: 0px;
        border-color: $azul;
        background-color: transparent;
      }

      button {
        width: 90%;
        margin: 0 1rem;
        margin-top: 1rem;
        padding: 0.9rem;
        font-size: 1.4rem;
        @include tablet {
          width: 20%;
        }

        i {
          color: $blanco;
        }
      }
    }

    .nuevo-grupo {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      justify-content: center;
      width: 100%;
      font-size: 1.4rem;
      @include tablet {
        display: flex;
        flex-direction: row;
        gap: 0rem;
        justify-content: space-between;
        width: 50%;
      }
      button {
        background: #0097ae;
        color: $blanco;
        cursor: pointer;
        margin-right: 0.5rem;
        padding: 0.5rem;
        border: none;
        flex: 4;

        &:last-of-type {
          margin-right: 0;
        }
      }

      .boton-grupo {
        flex: 4;
      }
    }
  }
}

.nuevo-grupo__mod {
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
  margin: 0 auto;

  @include tablet {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 1rem;
    width: 100%;
  }
  button {
    background: $amarillo;
    color: $blanco;
    cursor: pointer;
    margin-right: 1rem;
    padding: 0.8rem;
    border: none;
    width: 100%;
    &:last-of-type {
      margin-right: 0;
    }
  }
}
