.contenido-grupos {
  &::-webkit-scrollbar {
    width: 8px; /* Tamaño del scroll en vertical */
    height: 8px; /* Tamaño del scroll en horizontal */
    /* Ocultar scroll */
  }

  &::-webkit-scrollbar-thumb {
    background: $azul;
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: lighten($color: #0c407c, $amount: 8);
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
  }

  &::-webkit-scrollbar-thumb:active {
    background-color: lighten($color: #0c407c, $amount: 8);
  }

  @include grid(1, 3rem);
  width: 95%;
  margin: 0 auto;
  justify-items: center;
  height: 57rem;
  overflow: scroll;
  margin-top: 2rem;
  background-color: #fefefe;
  padding: 0rem;
  border-radius: 1.5rem;
  @include tablet {
    @include grid(5, 1rem);
    margin: 0;
    overflow: scroll;
    height: 46rem;
    width: 100%;
    margin-top: 1rem;
    padding: 1.5rem;
    // border-top: 0.3rem solid #006ca3;
  }
  .grupo {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 30rem;
    height: 32rem;
    background-color: $azul;
    border-radius: 10px;
    margin-top: 1rem;
    transition: transform 0.2s;
    position: relative;
    @include tablet {
      width: 21rem;
      height: 32rem;
    }

    h4 {
      margin: 0 2rem 0 0;
      text-transform: uppercase;
      color: $blanco;
      font-size: 1.8rem;
    }

    p {
      font-size: 1.5rem;
      color: $blanco;
      margin: 0;
    }

    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.8);
      transform: scale(1.1);
      .divIc {
        display: block;
      }
    }

    .container {
      padding: 2px 16px;
    }

    .grupo-imagen {
      height: 20rem;
      width: 100%;
      margin: 0;
      border-radius: 10px 80px 0px 80px;
    }
  }
}

.modal-agregar {
  display: none;
  position: fixed; /* Stay in place */
  padding-top: 6rem;
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.8); /* Black w/ opacity */

  .contenido-modal-grupo {
    width: 90%;
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    font-size: 1.2rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s;
    border-radius: 1.5rem;

    @include tablet {
      position: relative;
      background-color: #fefefe;
      margin: auto;
      font-size: 1.6rem;
      padding: 20px;
      border: 1px solid #888;
      width: 40%;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      -webkit-animation-name: animatetop;
      -webkit-animation-duration: 0.4s;
      animation-name: animatetop;
      animation-duration: 0.4s;
    }

    .encabezado-modal {
      color: $azul;

      h2 {
        @include tablet {
          font-size: 3rem;
          margin: 0;
          font-weight: $bold;
        }
        margin: 0;
        font-weight: $bold;
        font-size: 2rem;
      }
    }
  }

  .formulario-grupo {
    display: flex;
    flex-direction: column;
    font-size: 1.4rem;
    overflow: scroll;
    @include tablet {
      overflow: hidden;
    }
    input,
    select {
      padding: 1rem;
      border: none;
      background-color: $gris;
      color: $negro;
    }
    select {
      border: 2px solid darken($color: $gris, $amount: 8);
    }
    label {
      font-weight: $bold;
      margin: 8px 0;
    }

    button {
      @include boton($azul, block);

      font-size: 1.5rem;
      border-radius: 0.5rem;
      margin-top: 1.5rem;
    }

    .contenido-tipos {
      display: grid;
      grid-template-columns: 3fr 1fr;
      gap: 2rem;
      width: 100%;
      margin: 2rem 0 2rem 0;
      align-items: center;
      justify-content: center;

      select {
        width: 100%;
        margin-right: 4rem;
      }
    }
  }
  .close {
    color: #b6b3b3;
    font-size: 3rem;
    font-weight: bold;
  }

  .close:hover,
  .close:focus {
    color: #f50808e1;
    text-decoration: none;
    cursor: pointer;
  }

  /* Add Animation */
  @-webkit-keyframes animatetop {
    from {
      top: -300px;
      opacity: 0;
    }
    to {
      top: 0;
      opacity: 1;
    }
  }

  @keyframes animatetop {
    from {
      top: -300px;
      opacity: 0;
    }
    to {
      top: 0;
      opacity: 1;
    }
  }
}
.divIc {
  display: block;
  @include tablet {
    display: none;
  }
}
.btn-eliminar {
  position: absolute;
  width: 50%;
  height: 4rem;
  text-align: center;
  // margin: 1rem;
  margin-top: 0;
  font-size: 2.8rem;
  color: $blanco;
  background-color: #f83333;
  padding: 0.5rem;
  border-radius: 1rem 0;

  cursor: pointer;
  @include tablet {
    width: auto;
    height: auto;
    position: absolute;
    font-size: 1.8rem;
    // box-shadow: 2px 2px 2px #535353;
  }
  &:hover {
    transform: scale(1.1);
  }
}
